import React from 'react';
import './Tariffes.css';
import { Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import FormLabel from '../FormLabel/FormLabel';
import BackButton from '../BackButton/BackButton';
import Popup from '../Popup/Popup';
import AppButton from '../AppButton/AppButton';
import TariffesTemplate from '../TariffesTemplate/TariffesTemplate';
import { useSelector } from 'react-redux';
import { setDirection, setPayment } from '../../redux/actions/actions';
import { motion } from 'framer-motion';
import { directionVariants } from '../../utils/directionOptions';
import BurgerMenu from '../BurgerMenu/BurgerMenu';

function Tariffes() {
  const [value, setValue] = React.useState('');
  const [error, setError] = React.useState('');
  const [isTariffPopupHidden, setIsTariffPopupHidden] = React.useState(true);
  const navigate = useNavigate();
  const location = useLocation();
  const prices = useSelector((state) => state.prices);
  const currentUser = useSelector((state) => state.currentUser);
  const direction = useSelector((state) => state.direction);
  const isNolimit = currentUser.tariff === 'NOLIMIT';

  function handleChooseClick() {
    if (value.length) {
      setDirection(true);
      navigate(`/tariffes/${value}`);
    } else {
      handleError(' !');
    }
  }

  function handleError(text) {
    setError(text);
    setTimeout(() => setError(''), 5000);
  }
  function handleRedirectAndPayment() {
    if (
      value === 'fit' ||
      value === 'free' ||
      value === 'nolimit' ||
      value === ''
    ) {
      handleError(' !');
      return;
    } else {
      setDirection(true);
      setPayment(parseInt(value));
      navigate('/payment');
    }
  }
  return (
    <motion.section
      className='tariffes'
      initial={direction ? 'fromLeft' : 'fromRight'}
      animate={{ x: 0, opacity: 1, transition: { duration: 0.2, delay: 0.2 } }}
      exit={direction ? 'exitToRight' : 'exitToLeft'}
      variants={directionVariants}
    >
      {location.pathname === '/tariffes' && (
        <BackButton path={-1} text='' currentClass='wide' title='' />
      )}
      <BurgerMenu color='#348FF3' />
      <Routes>
        <Route
          exact
          path='/'
          element={
            <>
              <div className='tariffes-list'>
                <span className='tariffes-list__current-title'>
                   : {currentUser.tariff}
                  <br />
                  {currentUser.tariff === 'NOLIMIT'
                    ? '  ' + currentUser.endDate
                    : null}
                </span>
                <FormLabel
                  elementValue='free'
                  name='tariff'
                  handler={(data) => setValue(data)}
                  currentClass='tariff-item-free'
                  title='FREE'
                  text=' 10 , ,  , 
                   .'
                  valueMain={null}
                  isDiscounted={false}
                />
                <FormLabel
                  elementValue='fit'
                  name='tariff'
                  handler={(data) => setValue(data)}
                  currentClass='tariff-item-fit'
                  title='FIT'
                  text='      ?   ,     .'
                  valueMain={` ${prices.Fix_5} `}
                  isDiscounted={false}
                />
                <FormLabel
                  elementValue='nolimit'
                  name='tariff'
                  handler={(data) => setValue(data)}
                  currentClass='tariff-item-nolimit'
                  title='NOLIMIT'
                  text='  .  ,   ,  . '
                  valueMain={` ${prices.Nolimit_12} `}
                  isDiscounted={false}
                />
                <Popup
                  title=' '
                  currentClass='popup-tariff'
                  isHidden={isTariffPopupHidden}
                  handleHide={setIsTariffPopupHidden}
                >
                  {
                    <>
                      <div className='tariffes__popup-overlay' />
                      <div className='tariffes__popup-container'>
                        <p className='tariffes__popup-text'>
                          1.      .{' '}
                        </p>
                        <p className='tariffes__popup-text'>
                          2.   100%     
                           robo.{' '}
                        </p>
                        <p className='tariffes__popup-text'>
                          3.         ,
                                 
                                  
                           .      
                           ""  robo.   
                          ,     
                          .{' '}
                        </p>
                        <p className='tariffes__popup-text'>
                          4     
                          .{' '}
                        </p>
                        <p className='tariffes__popup-text'>
                          5.      
                          ,      .  
                              .  
                           .{' '}
                        </p>
                        <p className='tariffes__popup-text'>
                          6.     ,  
                            robo     
                           .     
                               
                          3  .{' '}
                        </p>
                        <p className='tariffes__popup-text'>
                          7.     1  
                           NOLIMIT (  1  = 1 ). 
                                
                                
                             .{' '}
                        </p>
                        <p className='tariffes__popup-text'>
                          8. robo,        
                                 ip,
                          ,  .    ,
                              . ,
                                , 
                           outline  256- .
                        </p>
                      </div>
                      <AppButton
                        text=''
                        currentClass='margin-top bg-blue primary white'
                        handler={() => setIsTariffPopupHidden(true)}
                      />
                    </>
                  }
                </Popup>
              </div>
              <div className='tariffes__content-secondary'>
                <span className='tariffes__error'>{error}</span>
                <div className='tariffes__button-box'>
                  <AppButton
                    text=''
                    currentClass='primary white bg-blue'
                    handler={handleChooseClick}
                  />
                </div>
                <span className='tariffes__tips'>
                        .
                </span>
              </div>
            </>
          }
        />
        <Route
          path='/free'
          element={
            <TariffesTemplate
              currentClass='free'
              buttonText=' '
              handler={() => {
                setDirection(true);
                navigate('/referral');
              }}
            >
              <div className='tariffes__free-content'>
                <h1 className='tariffes__free-title'>5+5=10</h1>
                <div className='tariffes__free-widgets'>
                  <div className='tariffes__free-widgets-row'>
                    <div className='tariffes__free-widget_small'>
                      <h2 className='tariffes__free-widget-title'>5 </h2>
                      <span className='tariffes__free-widget-text'>
                            FREE/FIT
                      </span>
                    </div>
                    <div className='tariffes__free-widget_small'>
                      <h2 className='tariffes__free-widget-title'>5 </h2>
                      <span className='tariffes__free-widget-text'>
                          ,   ,     
                        
                      </span>
                    </div>
                  </div>
                  {!isNolimit ? (
                    <div className='tariffes__free-widget-info'>
                      <p className='tariffes__free-widget-info-text'>
                           :{' '}
                        <span className='tariffes__free-widget-info-text_bold'>
                          {currentUser.traffic}
                        </span>
                      </p>
                      <p className='tariffes__free-widget-info-text'>
                         5 :{' '}
                        <span className='tariffes__free-widget-info-text_bold'>
                          {currentUser.endDate}
                        </span>
                      </p>
                      <p className='tariffes__free-widget-info-text'>
                          :{' '}
                        <span className='tariffes__free-widget-info-text_bold'>
                          {currentUser.trafficPerDay + '/'}
                        </span>
                      </p>
                      <p className='tariffes__free-widget-info-text_bold'>
                          ,  {' '}
                        {currentUser.trafficMonth ? '' : ''}  
                          
                      </p>
                    </div>
                  ) : null}
                </div>
              </div>
            </TariffesTemplate>
          }
        />
        <Route
          path='/fit'
          element={
            <TariffesTemplate
              currentClass='fit'
              buttonText=''
              handler={handleRedirectAndPayment}
              error={error}
            >
              <div className='tariffes__content-fit'>
                <FormLabel
                  elementValue={prices.Fix_5}
                  name='package'
                  handler={(data) => setValue(data)}
                  currentClass='tariff-item-fit'
                  title='5 '
                  text={null}
                  valueMain={`${prices.Fix_5} `}
                  valueSecondary=' '
                  isDiscounted={false}
                />
                <FormLabel
                  elementValue={prices.Fix_10}
                  name='package'
                  handler={(data) => setValue(data)}
                  currentClass='tariff-item-fit'
                  title='10 '
                  text={null}
                  valueMain={`${prices.Fix_10} `}
                  valueSecondary=' '
                  isDiscounted={true}
                  discountValue={
                    value === '' + prices.Fix_10
                      ? '    5%,    5 '
                      : ''
                  }
                />
                <FormLabel
                  elementValue={prices.Fix_20}
                  name='package'
                  handler={(data) => setValue(data)}
                  currentClass='tariff-item-fit'
                  title='20 '
                  text={null}
                  valueMain={`${prices.Fix_20} `}
                  valueSecondary=' '
                  isDiscounted={true}
                  discountValue={
                    value === '' + prices.Fix_20
                      ? '    40%,    5 '
                      : null
                  }
                />
              </div>
              <span className='tariffes__fit-text'>
                       ,   10
                  .  0 ,    5 .
              </span>
            </TariffesTemplate>
          }
        />
        <Route
          path='/nolimit'
          element={
            <TariffesTemplate
              currentClass='nolimit'
              buttonText=''
              handler={handleRedirectAndPayment}
              error={error}
            >
              <div className='tariffes__content-nolimit'>
                <FormLabel
                  elementValue={prices.Nolimit_1}
                  name='package'
                  handler={(data) => setValue(data)}
                  currentClass='tariff-item-nolimit'
                  title=''
                  text={null}
                  valueMain={` ${prices.Nolimit_1} /`}
                  isDiscounted={false}
                />
                <FormLabel
                  elementValue={prices.Nolimit_3}
                  name='package'
                  handler={(data) => setValue((data * 3).toString())}
                  currentClass='tariff-item-nolimit'
                  title='3 '
                  text={null}
                  valueMain={` ${prices.Nolimit_3} /`}
                  valueSecondary={`${prices.Nolimit_3 * 3} `}
                  isDiscounted={true}
                  discountValue=' 90'
                />
                <FormLabel
                  elementValue={prices.Nolimit_12}
                  name='package'
                  handler={(data) => setValue((data * 12).toString())}
                  currentClass='tariff-item-nolimit'
                  title='12 '
                  text={null}
                  valueMain={` ${prices.Nolimit_12} /`}
                  valueSecondary={`${prices.Nolimit_12 * 12} `}
                  isDiscounted={true}
                  discountValue=' 720'
                />
              </div>
            </TariffesTemplate>
          }
        />
      </Routes>
    </motion.section>
  );
}

export default Tariffes;
